	data() {
		return{
			img:[BANK_JH_DETAIL,BANK_BH_DETAIL,BANK_BJ_DETAIL],
			num:1
		}
	},
	methods:{},
	async onLoad(options) {
		_self=this
		_self.num = options.id
	}
};
